.todo-element {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 30px 20px 0;
  width: 70%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
  font-family: Roboto, sans-serif;
  font-weight: 100;
  font-size: 15px;
  color: black;
}

input[type="checkbox"] {
  margin-right: 10px;
}

.remove {
  border: none;
  margin-left: 10px;
  background-color: transparent;
  display: inline-block;
  font-size: 22px;
  padding: 2px;
}
.remove:hover {
  border: 2px solid grey;
  border-radius: 10px;
}

.completed {
  text-decoration: line-through;
}
